<template>
  <b-row class="m-0">
    <b-col cols="12">
      <b-row>
        <b-col cols="4">
          <b-row class="m-0">
            <b-col class="p-0">
              <div class="card bg-light mb-3">
                <div class="card-header bg-dark text-white" id="capture">
                  <h6 class="mb-0"><font-awesome-icon icon="plus-circle"/> Generate Concert Ticket/s</h6>
                </div>
                <div class="card-body bg-white">

                  <b-row align-v="center" class="loader" v-if="is_loading">
                    <b-col>
                      <b-spinner></b-spinner>
                      <br><br>
                      <strong>Loading</strong>
                    </b-col>
                  </b-row>
                  <b-form @submit="generate">
                    <b-form-group
                            label="Person In-charge:"
                            description="This field is required"
                    >
                      <select class="form-control" v-model="form.owner_id" required>
                        <option value="">Please Select</option>
                        <option value="-1">New Record</option>
                        <option v-for="row in owners" :value="row.id">
                          {{row.name}}
                        </option>
                      </select>
                    </b-form-group>
                    <b-form-group
                            label="Name:"
                            v-if="form.owner_id=='-1'"
                    >
                      <b-form-input
                              v-model="form.owner_name"
                              type="text"
                              description="This field is required"
                              required
                      ></b-form-input>
                    </b-form-group>
                    <b-form-group
                            label="Details:"
                    >
                      <b-form-input
                              v-model="form.name"
                              type="text"
                      ></b-form-input>
                    </b-form-group>

                    <b-form-group
                            label="Number of Ticket/s:"
                            description="This field is required"
                    >
                      <b-form-input
                              v-model="form.qty"
                              type="number"
                              required
                              min="1"
                      ></b-form-input>
                    </b-form-group>

                    <div class="text-right">
                      <b-button type="submit" variant="dark" size="sm"><font-awesome-icon icon="save" /> Generate</b-button>
                    </div>
                  </b-form>
                </div>
              </div>
            </b-col>
          </b-row>
          <div id="qr">

          </div>
        </b-col>
        <b-col v-if="tickets.length>0" cols="12">
          <div class="card bg-light mb-3">
            <div class="card-header bg-dark text-white d-flex flex-row-reverse align-items-center justify-content-between text-right"
            >
              <b-button type="button" @click="downloadImages()" variant="facebook" size="sm"><font-awesome-icon icon="save" /> PREVIEW & DOWNLOAD</b-button>
              <b-button type="button" @click="print()" variant="facebook" size="sm"><font-awesome-icon icon="print" /> PRINT</b-button>
            </div>
            <div class="card-body bg-white" style="overflow-x: auto;">
              <b-row  v-for="(ticket, index) in tickets">
                <b-col>
                  <b-row>


                    <b-col>
                      <div :id="'ticket-'+ticket.ticket_number" class="ticket-holder">
                        <span class="ticket-number">{{ticket.ticket_number}}</span>
                        <img :src="assets_path.DEFAULT_URL+'TICKET_TEMPLATE.png'" style="width:100%;border:1px solid #000;" />
                        <qrcode-vue :value="ticket.code" :size="size" level="H" style="position: absolute;top:68px;right:83px"/>
                      </div>
                    </b-col>
                    <!--<b-col cols="4">-->
                    <!--<qrcode-vue :value="ticket.code" :size="size" level="H" />-->
                    <!--</b-col>-->
                  </b-row>
                </b-col>
              </b-row>
            </div>
          </div>
        </b-col>
      </b-row>
    </b-col>
    <b-modal
            v-model="download"
            centered
            size="xl"
            body-class="p-0"
            :hide-footer="true"
            :hide-header="true"
    >
      <div class="card mb-0">
        <div
                class="card-header bg-dark text-white d-flex flex-row-reverse align-items-center justify-content-between text-right"
        >
          <b-button type="button" @click="downloadAll()" variant="facebook" size="sm"><font-awesome-icon icon="save" /> DOWNLOAD ALL</b-button>
        </div>
        <div class="card-body position-relative">
          <div id="qr-images">

          </div>
        </div>
      </div>
    </b-modal>
  </b-row>
</template>

<script>
  import {client_details,assets_path} from '@/config';
  import Loading from '@/views/widgets/Loading.vue';
  import SwalMixin from '@/views/mixins/Swal.js'
  import QrcodeVue from 'qrcode.vue'
  import html2canvas from 'html2canvas';

  export default {
    name: 'Login',
    mixins:[SwalMixin],
    components: {Loading,QrcodeVue},
    data(){
      return{
        size: 300,
        form:{
          owner_name:'',
          owner_id:'',
          name:'',
          qty:''
        },
        assets_path:{},
        tickets:[],
        download:false,
        is_loading:false,
        result:{},
        owners:[]
      }
    },
    watch:{
    },
    created() {
      this.assets_path = assets_path;
      this.getOwners();
    },
    methods:{
      downloadImage(data, filename = 'untitled.jpeg') {
        var a = document.createElement('a');
        a.href = data;
        a.download = filename;
        document.body.appendChild(a);
        a.click();
      },
      downloadAll(){
        this.tickets.forEach((element) => {
          var canvas = document.querySelector('#canvas-'+element.ticket_number);

          var dataURL = canvas.toDataURL("image/jpeg", 1.0);
          this.downloadImage(dataURL, 'ticket-'+element.ticket_number+'.jpeg');
        });

        this.download=false;
      },
      downloadImages(){
        this.download=true;
        this.tickets.forEach((element) => {
          const printCanvas = html2canvas(document.querySelector("#ticket-"+element.ticket_number)).then(function(canvas) {
            canvas.id = "canvas-"+element.ticket_number;
            document.querySelector("#qr-images").appendChild(canvas);
          });
        });
      },
      print(){
        window.open('/ticket-print/'+this.result.id, '_blank').focus();
      },
      generate(){
        event.preventDefault()
        this.swalConfirmSubmit((data)=>{
          if (data.value) {
            this.is_saving=true;
            this.axios.post('ticket/generate',this.form).then(response => {
              //this.swalTransactionCompleted();
              this.tickets=response.data.tickets;
              this.result=response.data;
            }).catch(error => {
              console.log(error)
              this.swal_transaction_error_data.text = error.response.data;
              this.swalRequestError();
            });
          }
        });
      },
      getOwners() {
        this.is_loading = true;
        this.axios.get('ticket/owners').then(response => {
          this.owners = response.data;
          this.is_loading = false;
        }).catch(error => console.log(error));
      },
    }
  }
</script>
<style>
  #qr-images canvas{
    width: 100%!important;
    height:auto!important;
  }
  .ticket-number{
    font-size: 61px;
    position: absolute;
    top: 90px;
    left: 533px;
    color: #000;
  }
  .ticket-holder{
    width: 1284px;
    position:relative
  }
</style>
